@use "sass:math";
@use "sass:map";
@import "../PageLoadAnimation/style.module.scss";
@import "./style.module.scss";
#home {
  position: relative;
  padding: 0;
}
@keyframes rotate {
  0% {
    rotate: 0deg;
  }

  100% {
    rotate: 360deg;
  }
}
@keyframes scale {
  0% {
    scale: 0;
  }

  100% {
    scale: 1;
  }
}
.scale_to_49 {
  scale: math.div(49, 304);
}

@keyframes scaleUp {
  0% {
    scale: 0;
  }
  5% {
    scale: math.div(49, 304);
  }
  10% {
    scale: 0;
  }
  100% {
    scale: 0;
  }
}
@for $n from 1 through 100 {
  #home .background_hex:nth-child(#{$n}) {
    top: math.random() * 100%;
    left: math.random() * 100%;
    animation: scaleUp 60s ((2s * $n) + 3s) infinite;
  }
}
.background_hex {
  position: absolute;
  rotate: 90deg;
  fill: $accentColor;
  scale: 0;
}
#home {
  position: relative;
}
